import React, {useMemo, useState} from "react";
import DataTable from 'react-data-table-component';
import FilterComponent from "../../agents/FilterComponent";
import {customStyles} from "../../../../utils/Common";

const TableHotelBookings = (props) => {
  const columns = [
    {
      name: "Details",
      button: true,
      cell: row => <>
        <button onClick={() => props.click(row.booking_Id)} className="btn btn-link">
          <i className="fa fa-eye"></i>
        </button>
      </>,
      width: '70px'
    },
    {
      name: "Booking Id",
      selector: row => row.booking_Id,
      sortable: true,
      // width: '130px'
    },
    {
      name: "Agent Email",
      selector: row => row.agentEmail,
      sortable: true,
      width: '220px'
    },
    {
      name: "Payment Status",
      selector: row => <span className="text-capitalize">{row.paymentStatus}</span>,
      sortable: true,
      width: '160px'
    },
    {
      name: "Booking Status",
      selector: row => <span className="text-capitalize">{row.bookingStatus.toLowerCase()}</span>,
      sortable: true,
      width: '150px'
    },
    {
      name: "Check-in",
      selector: row => <span>{row.checkIN.split("T")[0]}</span>,
      sortable: true,
      width: '110px'
    },
    {
      name: "Check-out",
      selector: row => <span>{row.checkOut.split("T")[0]}</span>,
      sortable: true,
      width: '120px'
    },
    {
      name: "Total Amount",
      selector: row => row.totalAmount,
      sortable: true,
      width: '140px'
    },
    {
      name: "Total Room",
      selector: row => row.totalRooms,
      sortable: true,
      width: '130px'
    },
    {
      name: "Total Pax",
      selector: row => row.totalPax,
      sortable: true,
      width: '110px'
    },
  ];


  const [filterText, setFilterText] = React.useState("");
  const [resetPaginationToggle, setResetPaginationToggle] = React.useState(false);

  const filteredItems = props.data.filter(
    item => JSON.stringify(item).toLowerCase().indexOf(filterText.toLowerCase()) !== -1
  );

  const subHeaderComponent = useMemo(() => {
    const handleClear = () => {
      if (filterText) {
        setResetPaginationToggle(!resetPaginationToggle);
        setFilterText("");
      }
    };

    return (
      <>
        <FilterComponent onFilter={e => setFilterText(e.target.value)} onClear={handleClear} filterText={filterText}/>
      </>
    );
  }, [filterText, resetPaginationToggle]);

  return (
    <>
      <DataTable
        columns={columns}
        data={filteredItems}
        striped
        pagination
        subHeader
        subHeaderComponent={subHeaderComponent}
        customStyles={customStyles}
      />
    </>
  )
}
export default TableHotelBookings;
