import axios from "axios";
import {CCard, CCardHeader, CCardBody} from "@coreui/react";
import React, {useEffect, useState} from "react";

import TableHotelBookings from "./TableHotelBookings";
import {errorToast, getCookieValue, ipAddress, tokenName} from "../../../../utils/Common";

const FailureLogsHotels = () => {
  const [hotels, setHotels] = useState(null);

  function ReadHotelLogs() {
    const token = getCookieValue(tokenName);

    axios.get(ipAddress + "admin/hotel-booking-failed", {
      headers: {'Authorization': `Bearer ${token}`,}
    }).then((response) => {
      // console.log(response.data);

      if (response.data.error) {
        errorToast(response.data.message);
      } else {
        // console.log(response.data.recordset);
        console.log(response.data.recordset[0]);
        setHotels(response.data.recordset);
      }
    }).catch(e => {
      errorToast(e.message);
    });
  }

  useEffect(() => {
    ReadHotelLogs();
  }, []);

  function clickHandler() {

  }

  return (
    <CCard>
      <CCardHeader className="card-header-bg">
        <h4 className="mb-0 primary-color">Hotels Logs</h4>
      </CCardHeader>

      <CCardBody>
        {hotels && <TableHotelBookings data={hotels} click={clickHandler}/>}
      </CCardBody>
    </CCard>
  )
}

export default FailureLogsHotels;
